@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.range {
  display: flex;
  justify-content: center;
  align-items: center;
}

.range div {
  position: absolute;
  border-radius: 50%;
  z-index: -1;
  border: 1px solid black;
}

.azimut {
  z-index: -1;
  position: absolute;
  width: 900px;
  left: -450px;
}

.azimut div {
  z-index: -1;
  position: absolute;
  width: 900px;
}

* {
  margin: 0;
}

h6 {
  font-size: 1em;
}

.radar24 {
  height: 100%;
  width: 100%;
  margin-top: 12vmin;
  margin-left: 6px;
  font-family: 'Roboto', sans-serif;
}

.radar24__position {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.mapboxgl-map {
  width: 100%;
  height: 100%;
}

.radar24__marker {
  background: url(data:image/png;base64,);
  background-size: cover;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
}

.marker__name {
  position: relative;
  bottom: 30px;
  left: 25px;
  z-index: 1;
  font-size: 18px;
  font-weight: 900;
  color: #ffde71;
  cursor: pointer;
  transition: 0.5s;
  text-shadow: 0px 0 10px #000,  0 1px 1px #000,  -1px 0 1px #000,  0 -1px 1px #000;
}

.marker__name:hover {
  color: #b4fffb;
  text-shadow: 0px 0 10px #000,  0 5px 5px #000,  -1px 0 5px #000,  0 -1px 5px #000;
  transition: 0.5s;
}

.marker__name:active {
  transition: 0.25s;
}

.marker__table {
  font-size: 1rem;
  position: absolute;
  z-index: 10;
  cursor: pointer;
  background: inherit;
  border-radius: 15px;
  border: 1px solid rgba(43, 43, 43, 0.568);
  overflow: hidden;
  background-color: rgba(241, 241, 241, 0.671);
  box-shadow: inset 0 0 0 3000px rgba(255, 255, 255, 0.226);
}

.marker__table:hover {
  opacity: 0.8;
}

.marker__table:active {
  transform: translateY(3px);
  transition: 0.2s;
}

.radar24__marker__svg {
  fill: #e75234;
  cursor: pointer;
}

.radar24__marker__svg:hover {
  fill: #6b1405;
  transition: 0.5s;
}

.radar24__marker__svg:active {
  transition: 0.25s;
}

.marker__table p {
  text-align: justify;
  margin-top: 1px;
  margin-bottom: 1px;
  margin-left: 5px;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
  color: #1b1b1b;
}

.input__radar {
  display: flex;
  width: 100%;
  flex-direction: column;
  overflow: hidden;
  box-shadow: inset 0 0 0 3000px rgba(150, 150, 150, 0.192);
}

.input__radar buttonhover {
  transform: translateY(5px);
  transition: 1s;
}

.control__panel {
  position: absolute;
  right: 20px;
  top: 130px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  z-index: 9999;
  background-color: rgba(203, 203, 203, 0.61);
  border: 1px solid black;
}

.control__panel div {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.form {
  display: block;
}

.control_arrow {
  fill: #000;
  position: absolute;
  z-index: 9999;
  right: 20px;
  top: 80px;
  height: 20px;
  width: 40px;
  transition: 0.25s;
  cursor: pointer;
}

.control_arrow:hover {
  fill: #636363;
  transition: 0.25s;
}

.control_arrow:active {
  fill: #b8b8b8;
  transition: 0.25s;
  height: 17px;
  width: 37px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 23px;
  z-index: 1;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.center {
  height: 0;
  width: 0;
  z-index: -1;
}

